body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #323232;
}

/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE and Edge */
body {
  -ms-overflow-style: none;
}

html { 
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
  color: inherit;
}

p {
  color: rgb(209, 209, 209);
  font-size: 120%;
}

#title {
  text-align: center;
  position: absolute;
  font-size: 500%;
  color: rgb(0, 162, 255);
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#subtitle {
  color: rgb(209, 209, 209);
  position: absolute;
  text-align: center;
  font-size: 250%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-family: 'Source Code Pro', monospace;
}

#HeaderContainer {
  position: relative;
  text-align: center;
}

#MainImage {
  opacity: 0.8;
  width: 100%;
  height: 100vh;
  color: rgba(128, 128, 128, 0.226);
}

#arrow {
  width: 10vh;
}
#arrow-text{
  margin-top: -0.5px;
}

#arrow-container {
  text-align: center;
  color: white;
  position: absolute;
  left: 50%;
  margin-top: -5%;
  transform: translate(-50%, -50%);
}

#H1Title {
  color: rgb(0, 162, 255);
  font-size: 300%;
}

.About {
  margin-left: 5%;
  margin-right: 5%;
  height: 100vh;
}

#avatar {
  display: inline-block;
  width: 30%;
}

#AboutInfo {
  display: inline-block;
  width: 70%;
}